	var path = 'G:/GitHub/_private/laoui-bootstrap/doc/pages/components/panel/panel.html';
	var html = "<div class=\"markdown-body\"><h1 id=\"panel-\">Panel </h1>\n<p class=\"lead\"><code>&lt;ui-panel /&gt;</code>bootstrap panel </p>\n\n<h2 id=\"-\"></h2>\n<p><code>ui-panel</code><code>panel-body</code></p>\n<p><code>ui-panel-heading</code>panel-heading</p>\n\n<p><em>heading ui-paneleg<code>&lt;ui-panel heading=&quot;heading&quot;&gt;&lt;/ui-panel&gt;</code></em></p>\n<h2 id=\"-\"></h2>\n<table>\n<thead>\n<tr>\n<th></th>\n<th></th>\n<th></th>\n<th></th>\n</tr>\n</thead>\n<tbody>\n<tr>\n<td>type</td>\n<td>panel<code>type</code>,<code>default</code>,<code>success</code>,<code>info</code>,<code>warning</code>,<code>danger</code></td>\n<td>string</td>\n<td></td>\n</tr>\n<tr>\n<td>heading</td>\n<td>panel<code>ui-panel-heading</code></td>\n<td></td>\n<td></td>\n</tr>\n<tr>\n<td>noborder</td>\n<td>panelborder</td>\n<td>boolean</td>\n<td></td>\n</tr>\n<tr>\n<td>className</td>\n<td>cssclass</td>\n<td>className</td>\n<td></td>\n</tr>\n</tbody>\n</table>\n<h2 id=\"-\"></h2>\n<h3 id=\"-code-ui-panel-heading-code-panel-heading\"><code>ui-panel-heading</code> panel-heading</h3>\n<div class=\"bs-example\" style=\"background:#eee\">\n    <ui-panel>\n        <ui-panel-heading>\n            <h4> <code>ui-panel-heading</code>,panel-heading</h4></ui-panel-heading>\n        <div>\n            <code>panel-body</code>\n        </div>\n    </ui-panel>\n</div>\n<ui-clipboard clipboard-target=\"clipboard1\"></ui-clipboard>\n<div class=\"highlight\" id=\"clipboard1\">\n<pre class=\"html\"><code style=\"width:100%;color:#fff\">\n        <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">ui-panel</span>&gt;</span>\n            <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">ui-panel-heading</span>&gt;</span><span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">h4</span>&gt;</span> ui-panel-heading<span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">h4</span>&gt;</span><span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">ui-panel-heading</span>&gt;</span>\n            <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">div</span>&gt;</span>\n                panel-body\n            <span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">div</span>&gt;</span>\n        <span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">ui-panel</span>&gt;</span>\n</code></pre>\n</div>\n\n<h3 id=\"-code-type-code-panel-\"><code>type</code> panel </h3>\n<div class=\"bs-example\">\n    <!-- panel-default -->\n    <ui-panel type=\"default\">\n        <ui-panel-heading><code>type=&quot;default&quot;</code>panel </ui-panel-heading>\n    </ui-panel>\n    <!-- panel-primary -->\n    <ui-panel type=\"primary\" noborder=\"\">\n        <ui-panel-heading><code>type=&quot;primary&quot;</code> <code>noborder</code> border panel </ui-panel-heading>\n    </ui-panel>\n    <!-- panel-success -->\n    <ui-panel type=\"success\">\n        <ui-panel-heading><code>type=&quot;success&quot;</code>panel </ui-panel-heading>\n    </ui-panel>\n    <!-- panel-info -->\n    <ui-panel type=\"info\">\n        <ui-panel-heading><code>type=&quot;info&quot;</code>panel </ui-panel-heading>\n    </ui-panel>\n    <!-- panel-warning -->\n    <ui-panel type=\"warning\">\n        <ui-panel-heading><code>type=&quot;warning&quot;</code>panel </ui-panel-heading>\n    </ui-panel>\n    <!-- panel-danger -->\n    <ui-panel type=\"danger\">\n        <ui-panel-heading><code>type=&quot;danger&quot;</code>panel </ui-panel-heading>\n    </ui-panel>\n</div>\n<ui-clipboard clipboard-target=\"clipboard2\"></ui-clipboard>\n<div class=\"highlight\" id=\"clipboard2\">\n<pre class=\"html\"><code style=\"width:100%;color:#fff\">\n    <span class=\"hljs-comment\">&lt;!-- panel-default --&gt;</span>\n    <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">ui-panel</span> <span class=\"hljs-attr\">type</span>=<span class=\"hljs-string\">&quot;default&quot;</span>&gt;</span>\n        <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">ui-panel-heading</span>&gt;</span><span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">code</span>&gt;</span>type=&quot;default&quot;<span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">code</span>&gt;</span>panel <span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">ui-panel-heading</span>&gt;</span>\n    <span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">ui-panel</span>&gt;</span>\n    <span class=\"hljs-comment\">&lt;!-- panel-primary --&gt;</span>\n    <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">ui-panel</span> <span class=\"hljs-attr\">type</span>=<span class=\"hljs-string\">&quot;primary&quot;</span> <span class=\"hljs-attr\">noborder</span>=<span class=\"hljs-string\">&quot;&quot;</span>&gt;</span>\n        <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">ui-panel-heading</span>&gt;</span><span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">code</span>&gt;</span>type=&quot;primary&quot;<span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">code</span>&gt;</span> <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">code</span>&gt;</span>noborder<span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">code</span>&gt;</span> border panel <span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">ui-panel-heading</span>&gt;</span>\n    <span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">ui-panel</span>&gt;</span>\n    <span class=\"hljs-comment\">&lt;!-- panel-success --&gt;</span>\n    <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">ui-panel</span> <span class=\"hljs-attr\">type</span>=<span class=\"hljs-string\">&quot;success&quot;</span>&gt;</span>\n        <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">ui-panel-heading</span>&gt;</span><span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">code</span>&gt;</span>type=&quot;success&quot;<span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">code</span>&gt;</span>panel <span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">ui-panel-heading</span>&gt;</span>\n    <span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">ui-panel</span>&gt;</span>\n    <span class=\"hljs-comment\">&lt;!-- panel-info --&gt;</span>\n    <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">ui-panel</span> <span class=\"hljs-attr\">type</span>=<span class=\"hljs-string\">&quot;info&quot;</span>&gt;</span>\n        <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">ui-panel-heading</span>&gt;</span><span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">code</span>&gt;</span>type=&quot;info&quot;<span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">code</span>&gt;</span>panel <span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">ui-panel-heading</span>&gt;</span>\n    <span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">ui-panel</span>&gt;</span>\n    <span class=\"hljs-comment\">&lt;!-- panel-warning --&gt;</span>\n    <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">ui-panel</span> <span class=\"hljs-attr\">type</span>=<span class=\"hljs-string\">&quot;warning&quot;</span>&gt;</span>\n        <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">ui-panel-heading</span>&gt;</span><span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">code</span>&gt;</span>type=&quot;warning&quot;<span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">code</span>&gt;</span>panel <span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">ui-panel-heading</span>&gt;</span>\n    <span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">ui-panel</span>&gt;</span>\n    <span class=\"hljs-comment\">&lt;!-- panel-danger --&gt;</span>\n    <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">ui-panel</span> <span class=\"hljs-attr\">type</span>=<span class=\"hljs-string\">&quot;danger&quot;</span>&gt;</span>\n        <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">ui-panel-heading</span>&gt;</span><span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">code</span>&gt;</span>type=&quot;danger&quot;<span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">code</span>&gt;</span>panel <span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">ui-panel-heading</span>&gt;</span>\n    <span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">ui-panel</span>&gt;</span>\n</code></pre>\n</div>\n\n<h3 id=\"-bootstrap\"> bootstrap</h3>\n<p></p><p class=\"lead\"><code>ui-panel</code> bootstrap  panel bootstrap</p><p></p>\n<div class=\"bs-example\">\n    <!-- Bootstrap  -->\n    <div class=\"panel panel-primary\">\n        <div class=\"panel-heading\">Bootstrap </div>\n        <div class=\"panel-body\">\n            <code>ui-panel</code>BootstrapBootstrap \n        </div>\n        <div class=\"panel-footer\">Panel footer</div>\n    </div>\n</div>\n<ui-clipboard clipboard-target=\"clipboard3\"></ui-clipboard>\n<div class=\"highlight\" id=\"clipboard3\">\n<pre><code style=\"width:100%;color:#fff\">\n        <span class=\"hljs-comment\">&lt;!-- Bootstrap  --&gt;</span>\n<span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">div</span> <span class=\"hljs-attr\">class</span>=<span class=\"hljs-string\">&quot;panel panel-primary&quot;</span>&gt;</span>\n    <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">div</span> <span class=\"hljs-attr\">class</span>=<span class=\"hljs-string\">&quot;panel-heading&quot;</span>&gt;</span>Bootstrap <span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">div</span>&gt;</span>\n    <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">div</span> <span class=\"hljs-attr\">class</span>=<span class=\"hljs-string\">&quot;panel-body&quot;</span>&gt;</span>\n        <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">code</span>&gt;</span>ui-panel<span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">code</span>&gt;</span>BootstrapBootstrap \n    <span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">div</span>&gt;</span>\n    <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">div</span> <span class=\"hljs-attr\">class</span>=<span class=\"hljs-string\">&quot;panel-footer&quot;</span>&gt;</span>Panel footer<span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">div</span>&gt;</span>\n<span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">div</span>&gt;</span>\n\n</code></pre>\n</div>\n</div>";
	window.angular.module('ng').run(['$templateCache', function(c) { c.put(path, html) }]);
